import React from 'react';
import clsx from 'clsx';
import { Link } from "react-router-dom"
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import HomeIcon from '@material-ui/icons/Home';
import PersonIcon from '@material-ui/icons/Person';
import Avatar from '@material-ui/core/Avatar';
import avatar from "../sumit2.jpeg"
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import WorkIcon from '@material-ui/icons/Work';
import ContactMail from '@material-ui/icons/ContactMail';
import DeveloperBoardSharpIcon from '@material-ui/icons/DeveloperBoardSharp';
import { Box } from "@material-ui/core"
import Footer from "./Footer"


const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    menuButton: {
        marginRight: 8,
    },
    hide: {
        display: 'none',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: 'nowrap'
    },
    drawerOpen: {
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerClose: {
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        overflowX: 'hidden',
        width: theme.spacing(7) + 1,
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9) + 1,
        },
    },
    toolbar: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: theme.spacing(0, 1),
        // necessary for content to be below app bar
        ...theme.mixins.toolbar,
    },
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
    avatar: {
        display: "block",
        margin: "0.5rem auto",
        width: theme.spacing(13),
        height: theme.spacing(13)
    },
    menuSliderContainer: {

        background: "#511",
        height: "100%"


    },
}));
const menuItem = [
    {
        listIcon: <HomeIcon />,
        listText: "Home",
        listpath: "/home"
    },
    {
        listIcon: <PersonIcon />,
        listText: "About Me",
        listpath: "/about"
    },
    {
        listIcon: <DeveloperBoardSharpIcon />,
        listText: "Skills",
        listpath: "/skills"
    },
    {
        listIcon: <WorkIcon />,
        listText: "Projects",
        listpath: "/projects"
    },
    {
        listIcon: <ContactMail />,
        listText: "Contact Me",
        listpath: "/contact"

    }
]

export default function Navbar() {
    const classes = useStyles();
    // const theme = useTheme();
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    return (
        <div className={classes.root}>
            <CssBaseline />

            <Drawer

                variant="permanent"
                className={clsx(classes.drawer, {
                    [classes.drawerOpen]: open,
                    [classes.drawerClose]: !open,
                })}
                classes={{
                    paper: clsx({
                        [classes.drawerOpen]: open,
                        [classes.drawerClose]: !open,
                    }),
                }}
            >
                <Box className={classes.menuSliderContainer}
                    // onClick={handleDrawerClose}
                    component="div">
                    <div className={classes.toolbar}>
                        <IconButton style={{ color: "tomato" }} onClick={open ? handleDrawerClose : handleDrawerOpen}>
                            {/* {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />} */}
                            {open ? <ArrowBackIcon /> : <ArrowForwardIcon />}
                        </IconButton>
                    </div>

                    <Divider />


                    {open ? <Avatar className={classes.avatar} src={avatar} alt="Sumit mali Image Unable Upload"></Avatar> : null}
                    <List>
                        {menuItem.map((IsItem, key) => (
                            <ListItem button key={key} component={Link} to={IsItem.listpath}>
                                <ListItemIcon className={classes.listItem}>
                                    {IsItem.listIcon}
                                </ListItemIcon>
                                <ListItemText className={classes.listItem} primary={IsItem.listText}></ListItemText>
                            </ListItem>

                        ))}
                    </List>
                </Box>

                {open ? <Footer /> : null}
            </Drawer>

        </div>
    );
}
