import React from 'react'
import { makeStyles, withStyles } from "@material-ui/core/styles"
import SedIcon from "@material-ui/icons/Send"
import { TextField, Typography, Button, Grid, Box } from "@material-ui/core"
import Navbar from "./Navbar"



const InputField = withStyles({
    root: {
        "& label.Mui-focused": {
            color: "tomato"
        },
        "& label": {
            color: "tan",
        },
        "& .MuiOutlinedInput-root": {
            "& fieldset": {
                borderColor: "tan",
            },
            "&:hover fieldset": {
                borderColor: "tan",
            },
            "&.Mui-focussed fieldset": {
                borderColor: "tan"
            }

        }
    }
})(TextField);



const useStyles = makeStyles(theme => ({
    heading: {
        color: "tomato",
        textAlign: "center",
        marginBottom: "1.5rem",
        textTransform: "uppercase"
    },
    form: {
        top: "50%",
        left: "50%",
        transform: "translate(-50%,-50%)",
        position: "absolute"
    },
    button: {
        marginTop: "1rem",
        color: "tomato",

        border: "1px solid tomato"

    },
    footer: {
        position: "fixed",
        left: 0,
        bottom: 0,
        height: "15rem",
        width: "100%",
        background: "#233",
        color: "white",
        textAlign: "center"

    }
}))

const Contact = () => {
    const classes = useStyles()
    return (
        <>
            <Navbar />
            <Box component="div" style={{ marginLeft: "4rem" }}>
                <Grid container justify="center" >
                    <Box component="form" className={classes.form}>
                        <Typography className={classes.heading} varient="h5" >
                            Hire Me or Contact Me

                        </Typography>
                        <InputField
                            required={true}
                            fullWidth={true}
                            label="Name"
                            inputProps={{ style: { color: "white" } }}
                            variant="outlined"
                            margin="dense" size="medium"
                        />
                        <br />
                        <InputField
                            required={true}
                            fullWidth={true}
                            label="Email"
                            variant="outlined"
                            inputProps={{ style: { color: "white" } }}
                            margin="dense" size="medium"
                        />
                        <br />
                        <InputField
                            fullWidth={true}
                            label="Contact Number"
                            variant="outlined"
                            inputProps={{ style: { color: "white" } }}
                            margin="dense" size="medium"
                            type={Number}
                        />

                        <Button className={classes.button} varient="outlined" fullWidth={true} endIcon={<SedIcon />} >Contact Me</Button>
                    </Box>

                </Grid>
            </Box>

        </>
    )
}

export default Contact
