import React from 'react'


import { BottomNavigation, BottomNavigationAction, Box, Typography } from "@material-ui/core"
import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import { makeStyles } from "@material-ui/styles"

const useStyle = makeStyles({
    root: {
        "&.MuiBottomNavigation-root": {
            minWidth: 0,
            maxWidth: 240,

        }
    },
    "& .MuiSvgIcon-root": {
        fill: "tan",
        "&:hover": {
            fill: "tomato",
            fontSize: "1.8rem"
        }
    },
    copy: {
        width: 200,
        margin: "1rem 0 1rem 1.5rem",

        color: "tan"
    }
})


const Footer = () => {

    const classes = useStyle()
    return (
        <>
            <Box style={{ background: "#233" }}>
                <Typography className={classes.copy} style={{ background: "#233" }}>
                    &copy; CopyRight by Sumit mali
            </Typography>
                <BottomNavigation width="auto" style={{ background: "#233" }}>


                    <BottomNavigationAction
                        href="https://github.com/malisumit86"
                        className={classes.root}
                        style={{ padding: 0 }}
                        icon={<GitHubIcon />}
                    >
                    </BottomNavigationAction>
                    <BottomNavigationAction
                        href="https://www.linkedin.com/in/sumit-mali-5963341a8"
                        className={classes.root}
                        style={{ padding: 0 }}
                        icon={<LinkedInIcon />}
                    >
                    </BottomNavigationAction>
                    <BottomNavigationAction
                        href="https://www.instagram.com/sumitrmali18__vlk/"
                        className={classes.root}
                        style={{ padding: 0 }}
                        icon={<InstagramIcon />}
                    >
                    </BottomNavigationAction>


                </BottomNavigation>
            </Box>
        </>
    )
}

export default Footer
