import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Navbar from "./Navbar"
import prof from "../prof.jpg"


const useStyles = makeStyles({
    root: {
        maxWidth: 900,
        margin: "2rem 0.6rem 0 20%",
        background: "#233"
    },
    media: {
        height: "150px",
        width: "150px",
        marginLeft: "3rem",
        marginTop: "3rem",
        marginBottom: "2.5rem",
        border: "1.5px Solid white",
        // textAlign: "center",
        padding: "1rem",
        borderRadius: "70%"
    },
});

export default function About() {
    const classes = useStyles();

    return (
        <>
            <Navbar />
            <Card className={classes.root}>
                <CardActionArea>
                    <CardMedia
                        className={classes.media}
                        image={prof}
                        title="Contemplative Reptile"
                    />
                    <Divider />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="h2" style={{ color: "tomato", marginLeft: "2rem" }}>
                            SUMIT MALI  (Web Developer)
          </Typography>
                        <Typography variant="body2" color="textSecondary" component="p">

                            <Typography style={{ color: "tan", marginLeft: "3rem" }}>
                                Hello, I am Sumit Mali pursuing BE (3rd Year) Information Technoloy from Smt.Kashibai Navle College of Engineering SPPU PUNE University.
                                </Typography>

                            <Typography style={{ color: "tan", marginLeft: "3rem" }}>
                                I had done my HSC in Arts,Science and Commerce College Chopada, jalgaon
                                and SSC in Neharu Vidyalay Kingaon at Village.
                                </Typography>
                            <Typography style={{ color: "tan", marginLeft: "3rem" }}>
                                Now I'm having Skills Like web application development and Learning about Java and Database.
                                </Typography>
                        </Typography>
                    </CardContent>
                </CardActionArea>
                <CardActions>
                    <Button size="small" color="primary">
                        Share
        </Button>
                    <Button size="small" color="primary">
                        Learn More
        </Button>
                </CardActions>
            </Card>
        </>
    );
}
